<template>
    <div>
        <div>
            <b-modal id="modal-sm" size="sm" no-close-on-backdrop title="Confirm">
                <p class="my-2">
                    The tax might differ. Are you sure you want to continue ?
                </p>
                <template v-slot:modal-footer="{ cancel }">
                    <b-button size="sm" class="float-right mb-3 p-2 pl-5 pr-5" variant="outline-primary" @click="cancel()">
                        Cancel
                    </b-button>
                    <b-button size="sm" class="float-right mb-3 p-2 pl-5 pr-5" variant="primary" @click="placeOrder">
                        Confirm
                    </b-button>
                </template>
            </b-modal>
        </div>
        <div class="d-block header-section">
        <h1>
            <b>Print Order</b>
        </h1>
        </div>
        <div class="container-fluid productManagement">
        <!-- START: Card Data-->
        <div class="card" style="background: transparent;margin-bottom: 20px">
            <table
            id="tab"
            class="table manufactrer printOrderTable"
            style="background: transparent;margin-bottom:0"
            >
            <thead style="background: #FFF">
                <tr>
                    <th>Purchase Order Number</th>
                    <th>No of Print Orders</th>
                    <th>Product</th>
                    <th>Reel Size</th>
                    <th>No.of Reels</th>
                    <th>Total Stamps</th>
                </tr>
            </thead>
            <tbody>
                <tr class="manufacturer_add">
                    <td>
                        <input
                            class="form-control"
                            type="text"
                            v-model="purchaseOrderNumber"
                        />
                    </td>
                    <td>
                        <input
                            class="form-control"
                            type="number"
                            v-model="noOfPrintOrders"
                            min="1"
                            :disabled="this.editFlow"
                        />
                    </td>
                    <td>
                        <select
                            @change="changeProductName"
                            class="form-control"
                            v-model="selectedProductName" 
                            name="productName"
                        >
                        <option disabled selected value="0">-select-</option>
                        <option
                            v-for="item in productNameList"
                            :value="{ id: item.productId, text: item.productName }"
                            :key="item.productId"
                        >
                            {{ item.productName }}
                        </option>
                        </select>
                    </td>
                    <td>
                        <select
                        @change="getTotalStamp"
                        class="form-control"
                        v-model="selectedReelSize"
                        name="coilSize"
                        :disabled="!this.selectedProductName"
                        >
                        <option disabled selected value="0">-select-</option>
                        <option
                            v-for="item in reelSizeList"
                            :value="{ id: item.productId, text: item.productName }"
                            :key="item.productId"
                        >
                            {{ item.productName }}
                        </option>
                        </select>
                    </td>
                    <td>
                        <input
                            @change="getTotalStamp"
                            class="form-control"
                            type="number"
                            v-model="noOfReels"
                            min="1"
                            :disabled="!this.selectedProductName"
                        />
                    </td>
                    <td>
                        <span>{{ totalStamp }}</span>
                    </td>
                </tr>
            </tbody>
            </table>
        </div>
        <div class="card mb-2 pb-3">
            <div class="row">
                <div class="col-md-4">
                    <p class="mt-4 col-md-12">Print Partner</p>
                    <div class="col-md-12">
                        <select class="form-control" v-model="selectedPrintPartner">
                            <option disabled selected value="0">-select-</option>
                            <option v-for="item in printPartnerList" :value="item.productId"
                                    :key="item.productId"
                                >
                                    {{ item.productName }}
                            </option>
                        </select>
                    </div>
                </div>
                <div class="col-md-4">
                    <p class="mt-4 col-md-12">Facility Location</p>
                    <div class="col-md-12">
                        <select class="form-control" v-model="selectedFacilityLocation">
                            <option disabled selected value="0">-select-</option>
                            <option v-for="item in facilityLocationList" :value="item.productId"
                                    :key="item.productId"
                                >
                                    {{ item.productName }}
                            </option>
                        </select>
                    </div>
                </div>
                <div class="col-md-4">
                    <p class="mt-4 col-md-12"><label for="expectedDate" class="mb-0 text-dark">Expected Date</label></p>
                    <div class="form-group row mt-3 col-md-12">
                        <div class="col-md-12">
                            <b-input-group class="mb-3">
                                <b-form-input
                                    id="expectedDate"
                                    v-model="expectedDate"
                                    type="text"
                                    autocomplete="off"
                                ></b-form-input>
                                <b-input-group-append>
                                    <b-form-datepicker
                                    v-model="expectedDate"
                                    button-only
                                    right
                                    locale="en-US"
                                    button-variant="primary"
                                    ></b-form-datepicker>
                                </b-input-group-append>
                            </b-input-group>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md-12">
                <p class="mt-4">Comments</p>
                <div class="row col-md-12">
                    <textarea class="form-control" rows="5" v-model="comments"></textarea>
                </div>
            </div>
        </div>
        <div class="mb-2">
            <b-button v-b-modal.modal-sm
                class="float-right mt-2 mb-3 p-2 pl-5 pr-5"
                variant="primary"
                :disabled="!(this.selectedProductName && this.purchaseOrderNumber && this.noOfPrintOrders && this.noOfReels && this.selectedReelSize && this.selectedPrintPartner && this.selectedFacilityLocation && this.comments && this.expectedDate)"
            >
                {{ this.editFlow ? "UPDATE ORDER" : "PLACE ORDER"}}
            </b-button>
            <b-button v-b-modal.modal-sm
                class="float-right mt-2 mb-3 mr-2 p-2 pl-5 pr-5"
                variant="outline-primary"
                @click="redirectToHome"
            >
                CANCEL
            </b-button>
        </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
    data: () => ({
        editEnabled: false,
        purchaseOrderNumber: "",
        noOfPrintOrders: 0,
        selectedProductName: null,
        selectedReelSize: null,
        noOfReels: 0,
        pricePerStamp: 0.05,
        selectedPrintPartner: "",
        selectedFacilityLocation: "",
        totalStamp: 0,
        totalStampPrice: 0,
        totalCoils: 0,
        taxPercentage: 0,
        taxAmount: 0,
        shippingCharges: 0,
        productNameList: [],
        reelSizeList: [],
        printPartnerList: [],
        facilityLocationList: [],
        expectedDate: null,
        response: {},
        comments:"",
        editFlow: false,
    }),
    props:['printOrderId'],
    mounted() {
        if(this.printOrderId) {
            this.editFlow = true;
            axios.get("/PrintOrder", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                params: {printOrderId:this.printOrderId},
            }).then((response) => {
                this.response = response.data;
                this.purchaseOrderNumber = response.data.poNum;
                this.noOfPrintOrders = response.data.noOfOrders;
                this.noOfReels = response.data.noOfReels;
                this.selectedPrintPartner = response.data.printPartnerId;
                this.selectedFacilityLocation = response.data.warehouseId;
                this.comments = response.data.comments;
                this.expectedDate = response.data.expectedDate;
                this.getProducts();
                this.getReelSizes(response.data.productId);
            });
        } else {
            this.editFlow = false;
        }
    },
    created() {
        if(!this.editFlow)
            this.getProducts();
        this.getPrintPartners();
        this.getWarehouses();
        this.getShippingCharges();
        this.getTax();
    },
    computed: {
    },
    methods: {
        placeOrder() {
            var reqBody = {};
            if(this.editFlow) {
                reqBody = reqBody = {
                    "id": this.printOrderId,
                    "poNum": this.purchaseOrderNumber,
                    "expectedDate": this.expectedDate,
                    "noOfOrders":  parseInt(this.noOfPrintOrders),
                    "productId": parseInt(this.selectedProductName.id),
                    "reelSizeId": parseInt(this.selectedReelSize.id),
                    "noOfReels":  parseInt(this.noOfReels),
                    "comments": this.comments,
                    "warehouseId": parseInt(this.selectedFacilityLocation),
                    "printPartnerId": parseInt(this.selectedPrintPartner)
                };
            } else {
                reqBody = reqBody = {
                    "poNum": this.purchaseOrderNumber,
                    "expectedDate": this.expectedDate,
                    "noOfOrders":  parseInt(this.noOfPrintOrders),
                    "productId": parseInt(this.selectedProductName.id),
                    "reelSizeId": parseInt(this.selectedReelSize.id),
                    "noOfReels":  parseInt(this.noOfReels),
                    "comments": this.comments,
                    "warehouseId": parseInt(this.selectedFacilityLocation),
                    "printPartnerId": parseInt(this.selectedPrintPartner)
                };
            }
            axios.post("/PrintOrder",
                reqBody,
                {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                },
            ).then(() => {
                this.$router.push({ name: 'PrintOrders' });
            });
        },
        redirectToHome() {
            this.$router.push({ name: 'PrintOrders' });
        },
        changeProductName() {
            this.getReelSizes(this.selectedProductName.id);
        },
        getProducts() {
            axios.get("/Meta/GetProducts", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            }).then((response) => {
                var tempProductName = [];
                for (let key in response.data) {
                    tempProductName.push({'productId': key, 'productName': response.data[key]});
                }
                this.productNameList = tempProductName;
                if(this.editFlow) {
                    this.selectedProductName = { id: this.response.productId, text: this.response.productName };
                }
            });
        },
        getReelSizes(selectedProductId) {
            axios.get("/Meta/GetReelSizes", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                params: {productId:selectedProductId}
            }).then((response) => {
                var tempReelSize = [];
                for (let key in response.data) {
                    tempReelSize.push({'productId': key, 'productName': response.data[key]});
                }
                this.reelSizeList = tempReelSize;
                if(this.editFlow) {
                    this.selectedReelSize = { id: this.response.reelSizeId, text: this.response.reelSize };
                    this.getTotalStamp();
                }
            });
        },
        getPrintPartners() {
            axios.get("/Meta/GetPrintPartners", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                var tempPrintPartners = [];
                for (let key in response.data) {
                    tempPrintPartners.push({'productId': key, 'productName': response.data[key]});
                }
                this.printPartnerList = tempPrintPartners;
            });
        },
        getWarehouses() {
            axios.get("/Meta/GetWarehouses", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                var tempWarehouses = [];
                for (let key in response.data) {
                    tempWarehouses.push({'productId': key, 'productName': response.data[key]});
                }
                this.facilityLocationList = tempWarehouses;
            });
        },
        getShippingCharges() {
            axios.get("/Meta/GetShippingCharges", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                this.shippingCharges = parseInt(response.data);
            });
        },
        getTax() {
            axios.get("/Meta/GetTax", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                this.taxPercentage = parseInt(response.data);
            });
        },
        getTotalStamp() {
            this.totalStamp = this.noOfReels * parseInt(this.selectedReelSize.text);
            this.totalStampPrice = (this.pricePerStamp * this.totalStamp);
            // this.taxAmount = (this.taxPercentage*this.totalPrice)/100;
            // this.finalAmount = this.totalPrice + this.taxAmount + this.shippingCharges;
        }
    },
};
</script>
